<template>
  <div class="max-w-5xl mx-auto px-4 py-10">
    <h1 class="text-3xl font-bold tracking-tight mb-3">
      {{ $t('pages.terms.title') }}
    </h1>
    <p class="text-gray-600 mb-8">
      {{ $t('pages.terms.subtitle') }}
    </p>

    <div class="bg-white rounded-2xl shadow-sm border p-6 space-y-6 text-gray-700 leading-7">
      <section>
        <h2 class="text-xl font-semibold mb-2">{{ $t('pages.terms.sections.s1.title') }}</h2>
        <p>{{ $t('pages.terms.sections.s1.body') }}</p>
      </section>

      <section>
        <h2 class="text-xl font-semibold mb-2">{{ $t('pages.terms.sections.s2.title') }}</h2>
        <p>{{ $t('pages.terms.sections.s2.body') }}</p>
      </section>

      <section>
        <h2 class="text-xl font-semibold mb-2">{{ $t('pages.terms.sections.s3.title') }}</h2>
        <p>{{ $t('pages.terms.sections.s3.body') }}</p>
      </section>

      <section>
        <h2 class="text-xl font-semibold mb-2">{{ $t('pages.terms.sections.s4.title') }}</h2>
        <p>{{ $t('pages.terms.sections.s4.body') }}</p>
      </section>

      <section>
        <h2 class="text-xl font-semibold mb-2">{{ $t('pages.terms.sections.s5.title') }}</h2>
        <p>{{ $t('pages.terms.sections.s5.body') }}</p>
      </section>
    </div>
  </div>
</template>

<script setup>
const { t, locale } = useI18n()
const config = useRuntimeConfig()

const siteUrl = config.public.siteUrl || 'https://pin-code.com'
const pageUrl = computed(() => {
  const prefix = locale.value === 'en' ? '' : `${locale.value}/`
  return `${siteUrl}/${prefix}terms-conditions`
})

const termsSchema = computed(() => ({
  "@context": "https://schema.org",
  "@type": "WebPage",
  "name": t("pages.terms.seo.title"),
  "description": t("pages.terms.seo.description"),
  "url": pageUrl.value,
  "inLanguage": locale.value,
  "isPartOf": {
    "@type": "WebSite",
    "name": "PIN Code Platform",
    "url": siteUrl
  }
}))

useHead({
  title: t('pages.terms.seo.title'),
  meta: [
    { name: 'description', content: t('pages.terms.seo.description') },
    { property: 'og:title', content: t('pages.terms.seo.title') },
    { property: 'og:description', content: t('pages.terms.seo.description') },
    { property: 'og:type', content: 'article' },
    { property: 'og:url', content: pageUrl.value }
  ],
  link: [
    { rel: 'canonical', href: pageUrl.value }
  ],
  script: [
    {
      id: 'terms-jsonld',
      type: 'application/ld+json',
      innerHTML: JSON.stringify(termsSchema.value)
    }
  ],
  __dangerouslyDisableSanitizersByTagID: {
    'terms-jsonld': ['innerHTML']
  }
})
</script>
